.title {
  font-size: 100px;
  color: skyblue;
}

.header {
  width: 100%;
  height: 120px;
  background-color: yellowgreen;
}

.header_in {
  margin: 0 auto;
  width: 1100px;
  height: 120px;
  background-color: rgb(220, 231, 198);
  display: flex;
  justify-content: space-between;
}

.header_in .logo {
  width: 130px;
  height: 120px;
  background-color: violet;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header_in .nav {
  width: 850px;
  height: 120px;
  background-color: violet;
}

.header_in .nav ul {
  height: 120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.mainimg {
  width: 100%;
  height: 450px;
  background-color: rgb(50, 205, 182);
}

